import { useAuth } from './useAuth';

export const usePermissions = () => {
  const { permissions, profile } = useAuth();

  console.log('[DEBUG PERMISOS] üîç usePermissions - permisos finales:', permissions);
  console.log('[DEBUG PERMISOS] üîç usePermissions - total permisos:', permissions.length);

  /**
   * Verifica si el usuario tiene un permiso espec√≠fico
   * Reconoce permisos con sufijo :own como v√°lidos para operaciones b√°sicas
   */
  const hasPermission = (permission: string): boolean => {
    if (!permission) return true;

    // Verificaci√≥n directa
    if (permissions.includes(permission)) {
      console.log(`[DEBUG PERMISOS] ‚úÖ hasPermission("${permission}") = true (directo)`);
      return true;
    }

    // Si el permiso solicitado es de tipo :view, :create, :edit, :delete
    // tambi√©n aceptar la variante :own
    const ownVariant = `${permission}:own`;
    if (permissions.includes(ownVariant)) {
      console.log(`[DEBUG PERMISOS] ‚úÖ hasPermission("${permission}") = true (tiene ${ownVariant})`);
      return true;
    }

    console.log(`[DEBUG PERMISOS] ‚ùå hasPermission("${permission}") = false`);
    return false;
  };

  /**
   * Verifica si el usuario tiene un rol espec√≠fico
   */
  const hasRole = (role: string): boolean => {
    if (!role || !profile) return false;
    
    const result = profile.rol === role;
    console.log(`[DEBUG PERMISOS] hasRole("${role}") = ${result} (rol actual: ${profile.rol})`);
    
    return result;
  };

  /**
   * Verifica si el usuario puede leer un m√≥dulo
   * Acepta tanto module:view como module:view:own
   */
  const canRead = (module: string): boolean => {
    const viewPermission = `${module}:view`;
    const viewOwnPermission = `${module}:view:own`;
    
    const result = permissions.includes(viewPermission) || permissions.includes(viewOwnPermission);
    
    console.log(`[DEBUG PERMISOS] canRead("${module}") = ${result} (buscando ${viewPermission} o ${viewOwnPermission})`);
    
    return result;
  };

  /**
   * Verifica si el usuario puede escribir en un m√≥dulo
   * Acepta tanto module:create/edit/delete como module:create:own/edit:own/delete:own
   */
  const canWrite = (module: string): boolean => {
    const createPermission = `${module}:create`;
    const createOwnPermission = `${module}:create:own`;
    const editPermission = `${module}:edit`;
    const editOwnPermission = `${module}:edit:own`;
    
    const result = permissions.includes(createPermission) || 
           permissions.includes(createOwnPermission) ||
           permissions.includes(editPermission) ||
           permissions.includes(editOwnPermission);
    
    console.log(`[DEBUG PERMISOS] canWrite("${module}") = ${result}`);
    
    return result;
  };

  /**
   * Verifica si el usuario puede eliminar en un m√≥dulo
   * Acepta tanto module:delete como module:delete:own
   */
  const canDelete = (module: string): boolean => {
    const deletePermission = `${module}:delete`;
    const deleteOwnPermission = `${module}:delete:own`;
    
    const result = permissions.includes(deletePermission) || permissions.includes(deleteOwnPermission);
    
    console.log(`[DEBUG PERMISOS] canDelete("${module}") = ${result}`);
    
    return result;
  };

  const isAdmin = profile?.rol === 'Admin';

  return {
    permissions,
    hasPermission,
    hasRole,
    canRead,
    canWrite,
    canDelete,
    isAdmin,
  };
};
